<template lang="html">
  <div class="page" ref="popWrapper">
    <div class="content">
<a name="process32"></a>

<p><br></p>
<p><span class="rvts17">影刃（</span><span class="rvts50">Shadow Blade</span><span class="rvts17">）</span></p>
<p><img width="436" height="748" alt="" hspace="1" vspace="1" src="../../../assets/pic1/abcamber103.jpg"><span class="rvts48"></span></p>
<p><span class="rvts43"></span>
<br><span class="rvts21">不同于</span><span class="rvts20"></span><span class="rvts21">流氓</span><span class="rvts20"></span><span class="rvts21">，影刃虽然也属于近战敏捷型，具备高爆发物理伤害的职业。但是，影刃的伤害附带元素加成，这使得她会修行一些火元素派系，造成高爆发的元素伤害。</span>
<br><span class="rvts18"><br></span></p>
<div><table width="979" border="1" cellpadding="0" cellspacing="-1" style="border-width: 0px; border-collapse: collapse;">
<tr valign="top">
<td width="177" valign="middle" style="border-width : 1px; border-color: #000000; border-style: solid;">
<p><span class="rvts26">主属性</span></p>
</td>
<td width="212" valign="middle" style="border-width : 1px; border-color: #000000; border-style: solid;">
<p><span class="rvts26">主派系</span></p>
</td>
<td width="176" valign="middle" style="border-width : 1px; border-color: #000000; border-style: solid;">
<p><span class="rvts26">默认技能</span><span class="rvts27">1</span></p>
</td>
<td width="173" valign="middle" style="border-width : 1px; border-color: #000000; border-style: solid;">
<p><span class="rvts26">默认技能</span><span class="rvts27">2</span></p>
</td>
<td width="173" valign="middle" style="border-width : 1px; border-color: #000000; border-style: solid;">
<p><span class="rvts26">默认技能</span><span class="rvts27">3</span></p>
</td>
</tr>
<tr valign="top">
<td width="177" valign="middle" style="border-width : 1px; border-color: #000000; border-style: solid;">
<p><span class="rvts42">灵巧，智谋</span></p>
</td>
<td width="212" valign="middle" style="border-width : 1px; border-color: #000000; border-style: solid;">
<p><span class="rvts42">恶棍，火元素</span></p>
</td>
<td width="176" valign="middle" style="border-width : 1px; border-color: #000000; border-style: solid;">
<p><img width="64" height="64" alt="" hspace="1" vspace="1" src="../../../assets/pic1/abcamber104.jpg"><span class="rvts39"></span>
<br><span class="rvts21">变色龙斗篷</span></p>
</td>
<td width="173" valign="middle" style="border-width : 1px; border-color: #000000; border-style: solid;">
<p><img width="64" height="64" alt="" hspace="1" vspace="1" src="../../../assets/pic1/abcamber105.jpg"><span class="rvts39"></span>
<br><span class="rvts21">反冲</span></p>
</td>
<td width="173" valign="middle" style="border-width : 1px; border-color: #000000; border-style: solid;">
<p><img width="64" height="64" alt="" hspace="1" vspace="1" src="../../../assets/pic1/abcamber106.jpg"><span class="rvts39"></span>
<br><span class="rvts21">鸡爪</span></p>
</td>
</tr>
</table>
</div>
<p class="rvps2"><span class="rvts28"><br></span></p>
<p><br></p>
<p><br></p>

    </div>
  </div>
</template>
<script>
import BScroll from 'better-scroll'
export default {
  data () {
    return { clickcount : 1  };
  },
  methods: {
    _initScroll() {
      this.popScroll = new BScroll(this.$refs.popWrapper, {
        click: true,
        probeType: 3
      });
    },
    timer(){
      if (this.clickcount < 3) {
        this.popScroll.refresh()
        this.clickcount = this.clickcount + 1
      }
    }
  },
  created() {
    this.$nextTick(() => {
      this._initScroll();
    });
    setInterval(this.timer, 2000);
  }
};
</script>
<style>
@import '../../../../static/css/common.css';
</style>



